import { Link, useLocation } from "react-router-dom";
import { React, useState } from "react";
import { useMediaQuery } from 'react-responsive';
import { motion, AnimatePresence } from 'framer-motion';

const HeaderLayout = () => {
    const location = useLocation();
    const isHome = location.pathname === '/';

    const handlePageChange = () => {
        window.scrollTo({ top: 0, left: 0, behavior: 'smooth' });
    };

    const [showMobileNav, setShowMobileNav] = useState(false);

    const isDesktopOrLaptop = useMediaQuery({ query: '(min-width: 1224px)' });
    const isTabletOrMobile = useMediaQuery({ query: '(max-width: 1224px)' });

    const toggleMobileNav = () => {
        setShowMobileNav(!showMobileNav);
    };

    const MotionLink = motion(Link);

    return (
        <div>
            {/* Desktop Structure */}
            {isDesktopOrLaptop && (
                <header className='layoutDiv'>
                    <h2>Palace Of Cognition</h2>
                    <div className="headerButtons">
                        <AnimatePresence mode="popLayout">
                            {!isHome && (
                                <MotionLink
                                    layout
                                    key="home-link"
                                    initial={{ opacity: 0, scale: 0.8 }}
                                    animate={{ opacity: 1, scale: 1 }}
                                    exit={{ opacity: 0, scale: 0.8 }}
                                    onClick={handlePageChange}
                                    className="layoutButton"
                                    to={'/'}
                                    whileHover={{ scale: 1.05 }}
                                    whileTap={{ scale: 0.95 }}
                                >
                                    
                                </MotionLink>
                            )}
                        </AnimatePresence>
                        <MotionLink onClick={handlePageChange} className="layoutButton" to={'/books'} whileHover={{ scale: 1.05 }} whileTap={{ scale: 0.95 }}></MotionLink>
                        <MotionLink onClick={handlePageChange} className="layoutButton" to={'/movies'} whileHover={{ scale: 1.05 }} whileTap={{ scale: 0.95 }}></MotionLink>
                        <MotionLink onClick={handlePageChange} className="layoutButton" to={'/articles'} whileHover={{ scale: 1.05 }} whileTap={{ scale: 0.95 }}></MotionLink>
                        <MotionLink onClick={handlePageChange} style={{ borderColor: '#598eff', color: '#598eff' }} className="layoutButton" to={'/signup'} whileHover={{ scale: 1.05 }} whileTap={{ scale: 0.95 }}></MotionLink>
                    </div>
                </header>
            )}

            {/* Mobile Structure */}
            {isTabletOrMobile && (
                <header className='layoutDiv'>
                    <button className="mobileShowButton" onClick={toggleMobileNav}></button>
                    <div className={showMobileNav ? 'mobile-nav show' : 'mobile-nav'}>
                        <button className="mobileShowButton" onClick={toggleMobileNav}></button>
                        <AnimatePresence>
                            {!isHome && (
                                <MotionLink
                                    layout
                                    initial={{ opacity: 0 }}
                                    animate={{ opacity: 1 }}
                                    exit={{ opacity: 0 }}
                                    onClick={handlePageChange}
                                    className="layoutButton-mobile"
                                    to={'/'}
                                    whileTap={{ scale: 0.95 }}
                                >
                                    
                                </MotionLink>
                            )}
                        </AnimatePresence>
                        <MotionLink onClick={handlePageChange} className="layoutButton-mobile" to={'/books'} whileTap={{ scale: 0.95 }}></MotionLink>
                        <MotionLink onClick={handlePageChange} className="layoutButton-mobile" to={'/movies'} whileTap={{ scale: 0.95 }}></MotionLink>
                        <MotionLink onClick={handlePageChange} className="layoutButton-mobile" to={'/articles'} whileTap={{ scale: 0.95 }}></MotionLink>
                        <MotionLink onClick={handlePageChange} style={{ borderColor: '#598eff', color: '#598eff' }} className="layoutButton-mobile" to={'/signup'} whileTap={{ scale: 0.95 }}></MotionLink>
                    </div>
                </header>
            )}
        </div>
    );
};

export default HeaderLayout;

